import Headroom from "react-headroom";
import Typist from "react-typist";

import PageBanner from "./templateA/PageBanner";
import {personal} from './personal/data';
import PageBannerMenu from "./templateA/PageBannerMenu";
import PageContent from "./templateA/PageContent";
import PageProfileMenu from "./templateA/PageProfileMenu";
import ActivityContent from "./templateA/ActivityContent";
import Showcase from "./templateA/Showcase";
 
const override = css`
display: block;
margin: 0 auto;
border-color: red;
margin-top: 20px;
`;
const tDark = {
    type: "dark",
    primary: {
      //green
      main: "#80cbc4",
      dark: "#4f9a94",
      light: "#b2fef7",
    },
    secondary: {
      // red
      main: "#ffab91",
      dark: "#c97b63",
      light: "#ffddc1",
    },
    bg: "#212121",
    color: "#CCC",
  };
  
  const tLight = {
    type: "light",
    primary: {
      //green
      main: "#80cbc4",
      dark: "#4f9a94",
      light: "#b2fef7",
    },
    secondary: {
      // red
      main: "#ffab91",
      dark: "#c97b63",
      light: "#ffddc1",
    },
    bg: "#f5f5f5",
    color: "#FFF",
  };

const themeBlueLight = createMuiTheme({
    palette: createPalette({
        primary: {
            //green
            main: "#80cbc4",
            dark: "#4f9a94",
            light: "#b2fef7",
          },
          secondary: {
            // red
            main: "#ffab91",
            dark: "#c97b63",
            light: "#ffddc1",
          },
      }),
    typography: {
      fontFamily: ["Prompt", "sans-serif"].join(","),
    },
  });
  
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    marginTop: theme.spacing(-2),
    textAlign: "center",
    marginBottom: theme.spacing(0.5),
  },
  rootA: {
    marginTop: theme.spacing(-1),

    [theme.breakpoints.down("sm")]: {
      marginBottom: theme.spacing(1),
    },
    [theme.breakpoints.up("md")]: {
    },
    textAlign: "center",
  },
  rootB: {
    flexGrow: 1,
    marginBottom: theme.spacing(0.5),
    textAlign: "center",
  },
  paper_root: {
    minHeight: "100vh",
    height: "100%",
    backgroundColor: fade(themeBlueLight.palette.primary.main, 0.6)
  },
  paper_primary: {
    height: "100%",
  },
  paper_activity: {
    minHeight: "100vh",
    height: "100%",
    borderRadius: "25px"
  },
  paper_bg: {
    margin: "5px 5px",
    top: "5px",
  },
  banner: {
    [theme.breakpoints.down("sm")]: {
      marginTop: theme.spacing(1.8),
    },
    [theme.breakpoints.up("md")]: {
      marginTop: theme.spacing(2),
    },
  },
  paper_right: {
    minHeight: "100vh",
    height: "100%",
    [theme.breakpoints.down("sm")]: {
      marginTop: theme.spacing(3),
    },
    [theme.breakpoints.up("md")]: {
      marginLeft: theme.spacing(1),
    },
    backgroundColor: fade(themeBlueLight.palette.secondary.light, 0.8),
  },
  paper_side: {
    minHeight: "100vh",
    height: "100%",
    [theme.breakpoints.down("sm")]: {
      marginTop: theme.spacing(3),
    },
    [theme.breakpoints.up("md")]: {
      marginLeft: theme.spacing(0),
    },
    
  },
  paper_footer: {
    backgroundColor: fade(themeBlueLight.palette.secondary.main, 0.8),
    [theme.breakpoints.down("sm")]: {
      marginTop: theme.spacing(-3),
    },
    [theme.breakpoints.up("md")]: {},
    textAlign: "center",
    height: "80px",
  },
}));

const fontRegis =  {
    fontFamily: ["Prompt", "sans-serif"].join(","),
  };


function AppHomepage() {
  const [theme, setTheme] = React.useState(createMuiTheme({ palette: createPalette(tLight),
    typography: fontRegis}));
  const [bg, setBg] = React.useState(theme.bg);
  const [data, setData] = React.useState(personal);
  const classes = useStyles();
  const { currentUser } = useContext(AuthContext);
  const [user, setUser] = React.useState(currentUser);
  const [dataSheet, setDatasheet] = React.useState(null);
  const [dataActivity, setDataActivity] = React.useState(null);
  const [dataActivityDisplay, setDataActivityDisplay] = React.useState(null);
  const [dataCert, setDataCert] = React.useState(null);
  const [loading, setLoading] = React.useState(true);

  useEffect(() => {
    // Run! Like go get some data from an API.
    //getDataSheet();
    Tabletop.init({
      key: "1sP53HcRkCGtG9c5lDjVnbz_j7aAud94Gh7x2djVJr_Q",
      callback: (googleData) => {
        console.log("google sheet data --->", googleData);
          var act_data = [];//แสดงกิจกรรมหน้าแรก
          var i = 0;
          var show_act_dataT = [];
          var cert_data = [];
          var teaching_data = []; //ตารางสอน
        var subject_data = []; //รายวิชา
        var material_data = [];//เอกสารรายวิชา
        var material_data_section = [];
        var order_data = []; // คำสั่ง
        var act_data = [];//กิจกรรม
        var show_act_data = [];//แสดงกิจกรรมหน้าแรก
        var show_act_dataT = [];//
        var plc_data = [];
        var cert_data = [];//เกียรติบัตร
        var document_data =[];

        //
        var student_lives_data = [];
        var parent_meeting_data = [];
        var sdq_data = [];
        var lessonplan_data =[];
        var afterteaching_data =[];
        var classroom_research_data =[];

        //
        var sar_data = [];
        var idplan_data = [];
        var report_data = [];

        googleData.map((obj, index) => {
          if(obj.category==="กิจกรรมและการปฏิบัติหน้าที่"){
            act_data.push(obj);
            show_act_dataT.push(false);
          } else if(obj.category==="รายวิชาที่สอน"){
            subject_data.push(obj);
          } else if(obj.category==="คำสั่งปฏิบัติหน้าที่"){
            order_data.push(obj);
          } else if(obj.category==="ตารางสอน"){
            teaching_data.push(obj);
          } else if(obj.category==="ชุมชนการเรียนรู้ทางวิชาชีพ (PLC)"){
            plc_data.push(obj);
          }  else if(obj.category==="มอบหมายงาน/สื่อการเรียนรู้"){
            material_data.push(obj);
            material_data_section.push(obj.teachingSubjectsID);
          } else if(obj.category==="ผลงานและเกียรติบัตร"){
            cert_data.push(obj);
        } else {
            if(obj.documentType==="แผนการจัดการเรียนรู้"){
              lessonplan_data.push(obj);
            } else if (obj.documentType==="บันทึกหลังแผนการเรียนรู้"){
              afterteaching_data.push(obj);
            } else if (obj.documentType==="วิจัยในชั้นเรียน") {
              classroom_research_data.push(obj);
            } else if(obj.documentType==="รายงานการเยี่ยมบ้าน"){
              student_lives_data.push(obj);
            } else if(obj.documentType==="รายงานการประชุมผู้ปกครอง"){
              parent_meeting_data.push(obj);
            } else if(obj.documentType==="SDQ"){
              sdq_data.push(obj);
            } else if(obj.documentType==="SAR"){
              sar_data.push(obj);
            } else if(obj.documentType==="ID Plan"){
              idplan_data.push(obj);
            } else if(obj.documentType==="รายงานผลการปฏิบัติงาน"){
              report_data.push(obj);
            }
          }
        })
          setDataCert(cert_data);
          setDataActivityDisplay(show_act_dataT);
          setDataActivity(act_data);
          setDatasheet(googleData);
          setLoading(false);
      },
      simpleSheet: true,
    });
  }, []);

  const handleBg = (bg) => {
    if (bg) {
      setTheme(createMuiTheme({ palette: createPalette(tDark),typography: fontRegis}));
    } else {
      setTheme(createMuiTheme({ palette: createPalette(tLight),typography: fontRegis}));
    }
  };

  const [item, setItem] = React.useState("ข้อมูลทั่วไป");
  const handleDisplay = (item) => {
    setItem(item);
  };

  return (
    <MuiThemeProvider theme={theme}>
      <div
        className="App"
        style={{
          backgroundColor: theme.palette.bg,
          fontFamily: "Prompt",
        }}
      >
        <Grid item xs={12} sm={12} md={12} style={{ width: "100%" }}>
          <Headroom>
            <MenuAppBar data={data} user={user} setUser={setUser} theme={theme} handleBg={handleBg} ></MenuAppBar>
          </Headroom>
        </Grid>

        <Grid
          item
          xs={12}
          sm={12}
          md={12}
          container
          spacing={1}
          className={classes.root}
        >
          <Grid item xs={12} sm={12} md={3} container>
            <Grid item xs={12} sm={12} md={12}>
              <Paper className={classes.paper_root}>
              <p>{" "}</p>
                <Profile theme={theme} data={data}></Profile>
                <PageProfileMenu theme={theme} handleDisplay={handleDisplay}></PageProfileMenu>
              </Paper>
            </Grid>
          </Grid>
          <Grid item xs={12} sm={12} md={9} container>
            <Grid item xs={12} sm={12} md={12}>
              <div  className={classes.banner}>
              <PageBanner theme={theme} handleBg={handleBg}></PageBanner>
              </div>
            </Grid>
            <Grid item xs={12} sm={12} md={9} className={classes.rootA}>
              <Paper className={classes.paper_primary}
              style={{backgroundImage: `linear-gradient(to bottom, ${fade(
                theme.palette.primary.main,
                0.2)}  0%, ${fade(theme.palette.primary.main, 0.4)}  100%)`,
                  boxShadow: `0px 1px 0px 0px ${fade(theme.palette.primary.dark, 0.9)}`,
                  }}>
                <p>
                </p>
                <div style={{padding: "1px"}}>
                <PageBannerMenu handleDisplay={handleDisplay}></PageBannerMenu>
                </div>
                
                <div>
                  <PageContent item={item}></PageContent>
                </div>
              </Paper>
            </Grid>
            <Grid item xs={12} sm={12} md={3} className={classes.rootA} container>
                <Grid item xs={12} sm={12} md={12} >
              <Paper className={classes.paper_right}>
                  <p>{" "}</p>
                  <SideMenu theme={theme} handleDisplay={handleDisplay}></SideMenu>
              </Paper>
              </Grid>
            </Grid>
          </Grid>
        </Grid>

        <Grid
          item
          xs={12}
          sm={12}
          md={12}
          container
          spacing={1}
          className={classes.rootB}
        >
          <Grid item xs={12} sm={12} md={8} container>
            <Grid item xs={12} sm={12} md={12}>
            <Paper className={classes.paper_activity}>
                <p>
                </p>
                {loading ? (
            <div>
              <CardButton
                type={"topic"}
                label={"กิจกรรมและการปฏิบัติงาน"}
              ></CardButton>
              <MoonLoader
                css={override}
                size={60}
                color={theme.palette.secondary.main}
                loading={loading}
              />
              <Typography variant="subtitle2">กำลังโหลดข้อมูล</Typography>
            </div>
          ) : (
                dataActivity && (
                  <ActivityContent theme={theme} loading={loading} data={dataActivity} dataActivityDisplay={dataActivityDisplay}></ActivityContent>
                ))}
                  
              </Paper>
            </Grid>
          </Grid>
          <Grid item xs={12} sm={12} md={4} container>
          <Grid item xs={12} sm={12} md={12} >
              <div className={classes.paper_side}>
                  <p>{" "}</p>
                  {loading ? (
            <div>
              <CardButton
                type={"topic"}
                label={"ผลงานและเกียรติบัตร"}
              ></CardButton>
              <MoonLoader
                css={override}
                size={60}
                color={theme.palette.secondary.main}
                loading={loading}
              />
              <Typography variant="subtitle2">กำลังโหลดข้อมูล</Typography>
            </div>
          ) : (
            dataCert && (
              <Showcase data={dataCert} theme={theme} handleDisplay={handleDisplay}></Showcase>
              
                ))}
                  
              </div>
              </Grid>
          </Grid>
        </Grid>

        <Grid item xs={12} sm={12} md={12} container className={classes.rootB}>
          <Grid item xs={12} sm={12} md={12}>
            <Paper className={classes.paper_footer}>
              <p>
                Edit <code>src/App.js</code> and save to reload.
              </p>
            </Paper>
          </Grid>
        </Grid>
      </div>
    </MuiThemeProvider>
  );
}

export default AppHomepage;
