export default function GoogleLogin({theme,setUser}) {
  const [state, setState] = React.useState({
    checkedA: false,
    checkedB: true,
    checkedC: true,
  });

  
  const googleLogin = () => {
    var googleAuthProvider = new firebase.auth.GoogleAuthProvider();
    /*  googleAuthProvider.setCustomParameters({
       prompt: 'select_account'
    });*/
    authGoogle.signInWithPopup(googleAuthProvider).then((result) => {
      const userGoogle = result.user;
      const accessToken = result.user.accessToken;
      console.log(result.user);
      setUser(result.user);
    });
    //getDataSheet();
  };

  const googleLogout = () => {
    authGoogle.signOut().then((response) => {
      window.location.reload();
      window.open("https://mail.google.com/mail/u/0/?logout&hl=en", "_blank");
    });
  };

  return (
    <Fab
                        variant="extended"
                        onClick={googleLogin}
                        style={{
                          backgroundColor: "#FFF",
                          marginLeft: theme.spacing(1),
                        }}
                      >
                        <LazyLoadImage
                          src={googleic}
                          style={{ width: "30px" }}
                        />
                        &ensp;ลงชื่อเข้าใช้
                      </Fab>
  );
}