import React, { useEffect } from "react";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import Badge from "@material-ui/core/Badge";
import NotificationsIcon from "@material-ui/icons/Notifications";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import CardButton from "./items/CardButton";
import ActivityCard from "./items/ActivityCard";
import MultipleSelected from "./items/MultipleSelected";
import KeyboardArrowLeft from "@material-ui/icons/KeyboardArrowLeft";
import KeyboardArrowRight from "@material-ui/icons/KeyboardArrowRight";
import MobileStepper from "@material-ui/core/MobileStepper";
import { css } from "@emotion/core";
import HashLoader from "react-spinners/HashLoader";
import MoonLoader from "react-spinners/MoonLoader";
import {
  fade,
  makeStyles,
  createMuiTheme,
  Theme,
  ThemeProvider,
  MuiThemeProvider,
} from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  root: {
    padding: "5px",
    [theme.breakpoints.down("sm")]: {},
    [theme.breakpoints.up("md")]: {},
  },
  content: {
    marginTop: "5px",
  },
}));

const override = css`
  display: block;
  margin: 0 auto;
  border-color: red;
  margin-top: 20px;
`;
export default function ActivityContent({
  theme,
  handleDisplay,
  item,
  loading,
  data,
  dataActivityDisplay
}) {
  const classes = useStyles();
  const [renderActivityData, setRenderActivityData] = React.useState(null);

  useEffect(() => {
    console.log(data);
  }, []);

  const handleSelectedItems = (item) => {
    handleDisplay(item);
  };

  const [activeStep, setActiveStep] = React.useState(0);
  const [maxSteps, setMaxSteps] = React.useState(0);

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleSetRenderActivityData = (item) => {
    setRenderActivityData(item);
    setMaxSteps(item.length);
  };

  const [showActivityDataT, setShowActivityDataT] = React.useState(dataActivityDisplay);

  const handleActivity = (ind, isShow) => {
    var show_act_dataT = [];
    showActivityDataT.map((obj, index) => {
      if (index === ind) {
        show_act_dataT.push(true);
      } else {
        show_act_dataT.push(false);
      }
    });
    setShowActivityDataT(show_act_dataT);
    //console.log(show_act_dataT[ind]);
  };
  return (
    <div className={classes.root}>
      <Paper
        className={classes.activity}
        style={{
          alignItems: "center",
          flexDirection: "column",
          backgroundColor: fade(theme.palette.primary.light, 0.5),
          display: "flex",
          borderRadius: "25px",
        }}
      >
        <div style={{width: "100%"}}>
          <CardButton
            type={"activity"}
            label={"กิจกรรมและการปฏิบัติงาน"}
            handleDisplay={handleSelectedItems}
            data={data}
            setRenderData={handleSetRenderActivityData}
            setActiveStep={setActiveStep}
          ></CardButton>
        </div>
        <Grid item xs={12} sm={12} md={12}>
          <MultipleSelected
            data={data}
            perpage={6}
            setRenderData={handleSetRenderActivityData}
            setActiveStep={setActiveStep}
          ></MultipleSelected>
        </Grid>
        <Grid item xs={12} sm={12} md={12} container spacing={1}>
          {!loading &&
            renderActivityData &&
            (renderActivityData.length > 0 ? (
              renderActivityData[activeStep].map((obj, index) => (
                <Grid item xs={12} sm={4} md={4} key={index}>
                  <Paper
                    style={{ height: "100%", width: "100%", textAlign: "left" }}
                  >
                    <ActivityCard
                      type={0}
                      obj={obj}
                      index={index}
                      handleActivity={handleActivity}
                      showActivityDataT={showActivityDataT[index]}
                    ></ActivityCard>
                  </Paper>
                </Grid>
              ))
            ) : (
              <div
                style={{
                  width: "100%",
                  minHeight: "90vh",
                  display: "flex",
                  alignItems: "center",
                  textAlign: "center",
                }}
              >
                <Typography
                  gutterBottom
                  variant="subtitle1"
                  color="textPrimary"
                  style={{ width: "100%" }}
                >
                  ไม่พบกิจกรรม
                </Typography>
              </div>
            ))}
        </Grid>
      </Paper>

      <MobileStepper
          style={{
            width: "98%",
            marginTop: theme.spacing(1),
            background: "transparent",
            backgroundColor: theme.palette.primary.main,
            borderRadius: "5px",
          }}
          steps={maxSteps}
          position="static"
          variant="text"
          activeStep={activeStep}
          nextButton={
            <Button
              size="small"
              onClick={handleNext}
              disabled={activeStep === maxSteps - 1}
            >
              หน้าถัดไป
              {theme.direction === "rtl" ? (
                <KeyboardArrowLeft />
              ) : (
                <KeyboardArrowRight />
              )}
            </Button>
          }
          backButton={
            <Button
              size="small"
              onClick={handleBack}
              disabled={activeStep === 0}
            >
              {theme.direction === "rtl" ? (
                <KeyboardArrowRight />
              ) : (
                <KeyboardArrowLeft />
              )}
              หน้าก่อนหน้า
            </Button>
          }
        />
    </div>
  );
}
